<template>
  <!-- 人才招聘 -->
  <div class="invite-box">
    <!-- 头部 -->
    <Nav></Nav>
    <!-- 内容部分 -->
    <section class="c-pagetitle is-load">
      <div class="c-pagetitle_fixed">
        <div class="c-pagetitle_img">
          <!-- <div class="c-pagetitle_logo"><img src="static/picture/hero_mask.svg" alt="" data-pagespeed-url-hash="1087874229" onload="pagespeed.CriticalImages.checkImageForCriticality(this);"></div> -->
        </div>
      </div>
      <div class="c-pagetitle_cnt">
        <h2 class="c-pagetitle_title">
          <span class="en">人才招聘</span>
          <span class="ja">TALENT ERCRUITMENT</span>
        </h2>
      </div>
    </section>
    <div class="c-pagetitle_page">
      <div class="businessIntro">
        <h3 class="businessTitle">
          我们期待您的加入
          <div class="search_wrapper" id="btn">
            <input id="btn1" class="search" type="text" placeholder="| 搜索职位" v-model="search" style="outline: none" />
            <button style="outline: none" class="search-btn" @click="Search">搜索</button>
          </div>
        </h3>
      </div>
    </div>
    <main class="l-main" style="padding-top: 1px;">
      <!-- 搜索后的 -->
      <div class="job-wrapper" v-if="searchData.length > 0">
        <ul v-for="(item, index) in searchData" :key="index" data-aos="fade-up">
          <li>
            <p class="pay">{{ item.pay }}</p>
            <p class="post">{{ item.position }}</p>
          </li>
        </ul>
      </div>
      <!-- 搜索前的 -->
      <div class="job-wrapper" v-else>
        <ul v-for="(item, index) in list" :key="index" data-aos="fade-up">
          <li>
            <p class="pay">{{ item.pay }}</p>
            <p class="post">{{ item.position }}</p>
          </li>
        </ul>
      </div>

      <div class="top-wrapper">
        <Service></Service>
      </div>
      <!-- 联系我们 -->
      <Relation-i></Relation-i>
      <!-- 底部 -->
      <Footer></Footer>
    </main>
  </div>
</template>

<script>
import Footer from "../../components/common/Footer.vue";
import RelationI from "../../components/common/RelationI.vue";
import Service from "../../components/common/Service.vue";
import Nav from "../../components/Nav.vue";
export default {
  components: { Nav, Footer, RelationI, Service },
  data() {
    return {
      search: "",
      // 原本展示数据
      list: [],
      // 搜索后的展示数据
      searchData: [],
    };
  },

  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.$axios
        .post("http://47.243.109.87:8082/caiweiGw/recruiting/list")
        .then((res) => {
          this.list = res.data.page.list;
          console.log(res.data.page.list);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    Search() {
      // search 是 v-model="search" 的 search
      var search = this.search;
      if (search) {
        this.searchData = this.list.filter(function (product) {
          // 每一项数据
          // console.log(product)
          return Object.keys(product).some(function (key) {
            // 每一项数据的参数名
            // console.log(key)
            return (
              String(product[key])
                // toLowerCase() 方法用于把字符串转换为小写。
                .toLowerCase()
                // indexOf() 方法可返回某个指定的字符串值在字符串中首次出现的位置。
                .indexOf(search) > -1
            );
          });
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.c-pagetitle_img {
  background: url("../../assets/image/kv1.png") center/cover;
}
.search_wrapper {
  width: 6rem;
  height: 1.375rem;
  border: 0.0125rem solid #19ce89;
  margin-top: 15PX;
  padding-left: 0.25rem;
  box-sizing: border-box;
  border-radius: 0.625rem;
  display: flex;
  transition: width 2s;
  -webkit-transition: width 1s;
  display: flex;
  justify-content: space-around;
  align-items: center;
  .search {
    flex: 1;
    border: 0;
    font-size: 0.45rem;
    transition: width 2s;
    -webkit-transition: width 1s;
  }
  .search-btn {
    width: 2rem;
    height: 100%;
    border: 0;
    background: #19ce89;
    color: #fff;
    border-radius: 0.625rem;
    font-size: 0.45rem;
  }
}
.invite-box {
  .job-wrapper {
    display: flex;
    width: 100%;
    // background: saddlebrown;
    margin-top: 1.5375rem;
    padding-left: 1.875rem;
    box-sizing: border-box;
    flex-wrap: wrap;
    ul {
      display: flex;
      justify-content: space-evenly;
      li {
        width: 6.25rem;
        height: 6.25rem;
        box-shadow: 0px 0.0125rem 0.4375rem 0rem rgba(131, 151, 144, 0.1);
        background: #fff;
        border-radius: 0.25rem;
        margin-right: 0.75rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 0.7375rem;
        &:hover {
          box-shadow: 0.0125rem 0.05rem 0.075rem rgba($color: #000000, $alpha: 0.7);
        }
        .pay {
          font-size: 0.6rem;
          color: #323232;
        }
        .post {
          font-size: 0.3rem;
          color: #999;
        }
      }
    }
  }
  .top-wrapper {
    position: fixed;
    right: 0.625rem;
    bottom: 0.625rem;
  }
}
</style>
